<template>
  <div class="pa-3">
    <div class="d-flex justify-space-between align-center mb-5">
      <h2>
        <v-icon class="mr-2">mdi-office-building</v-icon>Información
        Institucional
      </h2>
    </div>
    <!--Dialogo de agregar servicios-->
    <v-row dense>
      <v-col cols="12" sm="12" md="12">
        <v-card shaped outlined>
          <v-card-text class="d-flex justify-space-between py-0 px-2">
            <v-row align="center" justify="center" class="d-flex align-center">
              <v-col cols="9">
                <v-form @submit.prevent="onSubmit">
                  <v-row>
                    <v-col md="12" cols="12">
                      <v-text-field
                        v-model="business.slogan"
                        label="Slogan"
                        placeholder="Concepto de empresa"
                        required
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col md="12" cols="12">
                      <v-textarea
                        label="Resumen"
                        v-model="business.summary"
                        outlined
                        rows="3"
                        row-height="50"
                        placeholder="Descripción de la empresa"
                        icon="mdi-pencil"
                        shaped
                      ></v-textarea>
                    </v-col>
                    <v-col md="12" cols="12">
                      <v-textarea
                        label="Misión"
                        v-model="business.mision"
                        outlined
                        rows="3"
                        row-height="50"
                        placeholder="Misión"
                        icon="mdi-pencil"
                        shaped
                      ></v-textarea>
                    </v-col>
                    <v-col md="12" cols="12">
                      <v-textarea
                        label="Visión"
                        v-model="business.vision"
                        outlined
                        rows="3"
                        row-height="50"
                        placeholder="Visión"
                        icon="mdi-pencil"
                        shaped
                      ></v-textarea>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col lg="12" md="12" cols="12" sm="12">
                      <v-textarea
                        label="Valores"
                        v-model="business.values.text"
                        outlined
                        rows="3"
                        row-height="30"
                        placeholder="Concepto de valores"
                        icon="mdi-pencil"
                        shaped
                      ></v-textarea>
                    </v-col>
                 <!--    <v-col lg="6" md="6" cols="12" sm="12">
                      <v-combobox
                        v-model="business.values.optvalues"
                        :items="items"
                        chips
                        clearable
                        label="Valores"
                        multiple
                        prepend-icon="mdi-account-supervisor-circle"
                        solo
                      >
                        <template
                          v-slot:selection="{ attrs, item, select, selected }"
                        >
                          <v-chip
                            v-bind="attrs"
                            :input-value="selected"
                            close
                            @click="select"
                            @click:close="remove(item)"
                          >
                            <strong>{{ item }}</strong
                            >&nbsp;
                          </v-chip>
                        </template>
                      </v-combobox>
                    </v-col> -->
                  </v-row>
                  <v-row>
                    <v-col md="12" cols="12">
                      <v-text-field
                        v-model="business.manager"
                        placeholder="Responsable de empresa"
                        label="Manager"
                        required
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.address"
                        placeholder="Ubicación de la empresa"
                        label="Dirección"
                        required
                      />
                    </v-col>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.phone"
                        placeholder="Teléfono"
                        label="Teléfono"
                        required
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.socials.facebook"
                        placeholder="Enlace a Fanpage"
                        label="Facebook"
                        required
                      />
                    </v-col>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.socials.instagram"
                        placeholder="Enlace a Instagram"
                        label="Instagram"
                        required
                      />
                    </v-col>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.socials.whatsapp"
                        placeholder="Enlace a Whatsapp"
                        label="Whatsapp"
                        required
                      />
                    </v-col>
                    <v-col md="6" cols="12">
                      <v-text-field
                        v-model="business.socials.catalogue"
                        placeholder="Enlace a Catalogo"
                        label="Catalogo"
                        required
                      />
                    </v-col>
                  </v-row>
                  <v-row justify="center">
                    <v-btn
                      color="red"
                      class="mx-md-1"
                      type="button"
                      dark
                      to="/person"
                      >Limpiar</v-btn
                    >
                    <v-btn
                      color="primary"
                      class="mx-md-1"
                      @click="onSubmit()"
                      :disabled="loader"
                      v-if="btnupdate === false"
                    >
                      <v-progress-circular
                        class="mr-2"
                        size="20"
                        v-if="loader"
                        indeterminate
                        color="white"
                      />
                      AGREGAR
                    </v-btn>
                    <v-btn
                      color="primary"
                      class="mx-md-1"
                      :disabled="loader"
                      @click="updateInformation()"
                      v-else
                    >
                      <v-progress-circular
                        class="mr-2"
                        size="20"
                        v-if="loader"
                        indeterminate
                        color="white"
                      />
                      ACTUALIZAR
                    </v-btn>
                  </v-row>
                </v-form>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>

      <v-row v-if="business.length === 0 && !loader">
        <v-col md="12" class="text-center">
          Aún no se han agregado información institucional, hágalo desde el
          botón "AGREGAR"
        </v-col>
      </v-row>
    </v-row>

    <Loader
      :show="loader"
      message="Cargando Información Institucional...."
    ></Loader>
  </div>
</template>

<script>
import Loader from "../../System/Loader";
export default {
  components: { Loader },
  data: () => ({
    items: ["Puntualidad", "Trabajo colaborativo", "Honestidad"],
    dialog: false,
    btnupdate: true,
    url: "/registrar-empresa",
    dialogEdit: false,
    loader: false,
    method: "POST",
    businessId: "",
    business: {
      active: "",
      id: "",
      created: "",
      slogan: "",
      summary: "",
      mision: "",
      vision: "",
      manager: "",
      address: "",
      phone: "",
      __v: "",
      _id: "",
      socials: { facebook: "", instagram: "", whatsapp:"", catalogue:"" },
      values: {
        text: "",
       // optvalues: [],
      },
    },

    headers: [
      { text: "Orden del dia", value: "text" },
      { text: "Opciones", value: "action", align: "right", sortable: false },
    ],
    search: "",
    page: 1,
    pageCount: 1,
    itemsPerPage: 5,
    selectedInformation: null,
  }),
  created() {
    this.page = 1;
    this.getInformations();
  },
  methods: {
    onSubmit() {
      console.log("el arreglo a enviar:", this.business);
      // if (!this.validate()) return;
      //      this.loader = true;
      //    this.form.role = this.form.role.toUpperCase();

      this.$http({
        method: "post",
        url: this.url,
        data: this.business,
      })
        .then((res) => {
          if (res.data && res.data.status === "success") {
            this.businessId = res.data.business[0].id;
            // on save user
            this.$alert.ok(`Información Institucional registrada con éxito`);
          }
          /* 
          if (this.method.toLowerCase() === "post") {
            alert("antes");

            if (res.data && res.data.status === "success") {
              alert("yes");
              // on save user
              this.$alert.ok(`${this.business} registrado con éxito`);
              this.clearForm();
            }
          } else {
            // on update user
            if (res.data && res.data.status && res.data.status === "success") {
              this.$alert.ok(res.data.message);
            }
          } */
        })
        .catch((err) => {
          if (err.response && err.response.data) {
            this.error = err.response.data.message;
          }
        })
        .finally(() => {
          this.loader = false;
        });
    },
    getInformations() {
      this.loader = true;
      this.$http
        .get(`/listar-empresa`)
        .then((res) => {
          if (res.data.business[0] == null) {
            this.btnupdate = false;
          } else {
            this.btnupdate = true;
            this.businessId = res.data.business[0].id;
            this.business = res.data.business[0];
          }
          //        alert("lenght", this.business.slogan.lenght)
        })
        .finally(() => (this.loader = false));
    },
    updateInformation() {
      var urln = "/actualiza-datos/" + this.businessId;
      this.$http({
        method: "put",
        url: urln,
        data: this.business,
      })
        .then((response) => {
          //cuando se guarda el registro
          if (response.data.error) {
            this.$alert.err(`eorr` + response.data.error);
          } else {
            this.$alert.ok(`Información Institucional actualizada con éxito`);
          }
        })
        .catch((error) => {
          //cuando hay un error al guardar
          this.errors = error.response.data;
          this.$alert.err(`eorr` + this.errors);
        });
    },
    editInformation(service) {
      this.selectedInformation = {
        ...service,
        text: service.text,
      };
      this.dialogEdit = true;
    },
    async deleteInformation(service) {
      const confirm = await this.$refs.confirm.open(
        "Eliminar Sesión",
        `¿Esta seguro que desea eliminar esta sesión?`
      );
      if (!confirm) return;
      this.$http
        .delete(`/eliminar-sesion/${service._id}`)
        .then((res) => {
          if (res.data && res.data.status === "success") {
            const index = this.business.indexOf(service);
            this.business.splice(index, 1);
            this.$alert.ok(res.data.message);
          } else {
            this.$alert.err("Upps! algo salió mal reintenta");
          }
        })
        .catch((err) => () => {
          if (err.response && err.response.data) {
            this.$alert.err(err.response.data.message);
          }
        });
    },
    remove(item) {
      this.business.values.optvalues.splice(
        this.business.values.optvalues.indexOf(item),
        1
      );
      this.business.values.optvaluess = [...this.business.values.optvalues];
    },
  },
};
</script>

<style scoped>
.service-link {
  text-decoration: none;
  text-transform: uppercase;
  color: #6a6a6b;
}
</style>
